<template>
    <main class="whole-body-container">
        <section class="body-container max-width flex">
            <label for="chooseState" class="relative">Select your favourite state!</label>
            <select name="Choose your state" id="chooseState"  @change="chooseState">
                caca
                <option :value="state" v-for="(state, index) in states" 
                :key="index" v-html="state"
                ></option>
            </select>

            <section class="clubs-container relative flex">
                <article class="club relative" v-for="(club, index) in stateClubs" :key="index" v-show="stateClubs">
                    <h2 v-html="club.club"></h2>
                    <hr>
                    <p v-html="club.city ? club.city : 'No city available'"></p>
                    <hr>
                    <p v-html="club.league ? club.league : 'No league available'"></p>
                </article>
            </section>
            <section class="clubs-container relative flex" >
                <article class="club relative" v-for="(club, index) in moreStateClubs" :key="index" v-show="moreStateClubs">
                    <h2 v-html="club.club"></h2>
                    <hr>
                    <p v-html="club.city ? club.city : 'No city available'"></p>
                    <hr>
                    <p v-html="club.league ? club.league : 'No league available'"></p>
                </article>
            </section>
        </section>
    </main>
</template>

<script>
import axios from 'axios'

export default {
    name:'GetTeams',
    data(){
        return{
            states:[],
            state:'',
            stateClubs:false,
            moreStateClubs:false,
        }
    },
    methods:{
        setOptions(){
            axios
                .get(`https://www.upbringo.com/api/assignment/football/?state=${this.state}&nextPageToken=0`)
                .then(response => {
                    //set states as options
                    let APIstates = response.data.states
                    APIstates.forEach(st => this.states.push(st))
                })                
        },
        getClubs(){
            axios
                .get(`https://www.upbringo.com/api/assignment/football/?state=${this.state}&nextPageToken=`)
                .then(response => {
                    //console.log(response)
                    //check if user chose a state, and display clubs of that one
                    let stateClubs = response.data.clubs
                    if(this.state.length > 1){
                        this.stateClubs = stateClubs
                    }
                })
                .catch(err => {
                    let modal = document.querySelector('.whole-error-container')
                    modal.style.display = 'block'
                })
        },
        chooseState(e){
            //set chosen state so api can make the proper call
            this.state = e.target.value
            this.getClubs()

            //reset list so it is not shown
            this.moreStateClubs = false
        },
        onScroll(){
                window.onscroll = () => {
                let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight === document.documentElement.offsetHeight;
                let loader = document.querySelector('.whole-loader-container')

                if (bottomOfWindow) {
                    axios
                    .get(`https://www.upbringo.com/api/assignment/football/?state=${this.state}&nextPageToken=1`)
                    .then(response => {
                        //add new clubs to first list
                        let moreClubs = response.data.clubs
                        for(let i = 0; i < moreClubs.length; i++){
                            if(response.data.clubs.length > 1){
                                loader.style.display = 'block'
                                setTimeout(function(){loader.style.display = 'none'}, 500)
                                this.moreStateClubs = moreClubs
                            }    
                        }
                    });                    
                }
            }
        }
    },
    mounted(){
        this.setOptions()
        this.onScroll()
    }
}
</script>

<style scoped lang="scss">
@import '../variables/variables.scss';

.whole-body-container{

    .body-container{
        padding:2rem;
        flex-direction: column;
        align-items: flex-start;

        label{
            text-align: center;
            font-size: 1.6rem;
            font-weight: 500;
            left:50%;
            transform:translateX(-50%);
            margin-bottom: 1rem;
        }

        select{
            width:100%;
            font-size: 2rem;
            outline:none;
            box-shadow: $boxShadow;
            border-radius: 200px;
            padding:0 1rem;
            cursor:pointer;
            margin-bottom: 2rem;

            option{
                width:50%;
            }
        }

        .clubs-container{
            margin:0;
            width:100%;
            flex-wrap: wrap;
            justify-content: space-around;

            .club{
                box-shadow: $boxShadow;
                margin:2rem 0;
                border-radius: .5rem;
                padding:1rem;
                background:$white;
                width: 100%;
                max-width: 30rem;

                h2{
                    font-weight: 500;
                    font-size: 1.8rem;
                }

                hr{
                    margin:.3rem 0;
                }
                p{
                    font-size: 1.2rem;
                }
            }
        }
    }
}
</style>