<template>
  <header class="whole-header-container relative">
    <section class="header-container max-width flex">
      <div class="logo">
        <img src="../assets/icons/logo.png" alt="logo">
      </div>
      <nav>
        <ul class="flex">
          <li @click="alert">Home</li>
          <li @click="alert">About</li>
          <li @click="alert">Contact</li>
        </ul>
      </nav>
      <div class="burguer" @click="toggleNav">
        <i :class="menu"></i>
      </div>
    </section>
  </header>
</template>

<script>
export default {
  name: 'Header',
  data(){
    return{
      menu:'fas fa-bars'
    }
  },
  methods:{
    toggleNav(){
      let nav = document.querySelector('nav')

      //toggle menu class
      this.menu == 'fas fa-bars' ? this.menu = 'fas fa-times' : this.menu = 'fas fa-bars'

      //translate nav
      nav.classList.toggle('navOpen')
    },
    alert(){
      alert('oops, nothing to see here')
    }
  }
}
</script>

